<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;企业信息
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" :rules="rules" label-width="100px">
          <el-form-item label="企业名称" prop="name">
            <el-input  placeholder="请输入企业名称" v-model="form.name" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="logo" prop="name">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreview"
              :on-success="handleAvatarSuccess">
              <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="企业地址" prop="address">
            <el-input  placeholder="请输入企业地址" v-model="form.address" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="企业电话" prop="phone">
            <el-input  placeholder="请输入企业电话" v-model="form.phone" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="企业邮箱" prop="email">
            <el-input  placeholder="请输入企业邮箱" v-model="form.email" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="企业简介" prop="introduction">
            <el-input type="textarea"  placeholder="请输入企业简介" v-model="form.introduction" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="地址图片" prop="wxgzh">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreviewA"
              :on-success="handleAvatarSuccessA">
              <img v-if="addimg" :src="img+addimg" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="微信二维码" prop="wxgzh">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreview2"
              :on-success="handleAvatarSuccess2">
              <img v-if="imageUrl2" :src="img+imageUrl2" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-divider content-position="left">分公司一</el-divider>
          <el-form-item label="分公司名称" prop="name1">
            <el-input  placeholder="请输入分公司名称" v-model="form.name1" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="分公司地址" prop="address1">
            <el-input  placeholder="请输入分公司地址" v-model="form.address1" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="分公司电话" prop="phone1">
            <el-input  placeholder="请输入分公司电话" v-model="form.phone1" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="地址图片" prop="wxgzh">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreviewA1"
              :on-success="handleAvatarSuccessA1">
              <img v-if="addimg1" :src="img+addimg1" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-divider content-position="left">分公司二</el-divider>
          <el-form-item label="分公司名称" prop="name2">
            <el-input  placeholder="请输入分公司名称" v-model="form.name2" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="分公司地址" prop="address2">
            <el-input  placeholder="请输入分公司地址" v-model="form.address2" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="分公司电话" prop="phone2">
            <el-input  placeholder="请输入分公司电话" v-model="form.phone2" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="地址图片" prop="wxgzh">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreviewA2"
              :on-success="handleAvatarSuccessA2">
              <img v-if="addimg2" :src="img+addimg2" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-divider content-position="left">分公司三</el-divider>
          <el-form-item label="分公司名称" prop="name3">
            <el-input  placeholder="请输入分公司名称" v-model="form.name3" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="分公司地址" prop="address3">
            <el-input  placeholder="请输入分公司地址" v-model="form.address3" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="分公司电话" prop="phone3">
            <el-input  placeholder="请输入分公司电话" v-model="form.phone3" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="地址图片" prop="wxgzh">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreviewA3"
              :on-success="handleAvatarSuccessA3">
              <img v-if="addimg3" :src="img+addimg3" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <el-button style="margin-left: 100px"  type="primary" @click="sub('form')">提交</el-button>
        <el-button style="margin-left: 100px"  type="" @click="$router.go(-1)">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'
  export default {
    name: "qyinfo",
    data:function () {
      return{
        type:0,
        img:window.HOST+'/',
        addimg:'',
        addimg1:'',
        addimg2:'',
        addimg3:'',
        form:{

        },
        rules:{
          address:[{
            required: true,
            message: '请输入企业地址',
            trigger: 'blur'
          }],
          phone:[{
            required: true,
            message: '请输入企业电话',
            trigger: 'blur'
          }],
          introduction:[{
            required: true,
            message: '请输入企业简介',
            trigger: 'blur'
          }],
          name:[{
            required: true,
            message: '请输入企业名称',
            trigger: 'blur'
          }]
        },
        uploadUrl:window.file,
        imageUrl:'',
        imageUrl2:''
      }
    },
    created() {
      this.getDetails()
      // this.getBrandList();
    },
    activated(){
      this.getDetails()
    },
    methods:{
      //详情
      getDetails(){
        var that = this;
        this.apiPost('/admin/showqyinfo',{}).then((res)=>{
          this.handelResponse(res,(data)=>{
            that.form = data.data;
            that.imageUrl = that.form.logo;
            that.imageUrl2 = that.form.mpic;
            that.addimg = that.form.addimg;
            that.addimg1 = that.form.addimg1;
            that.addimg2 = that.form.addimg2;
            that.addimg3 = that.form.addimg3;
          })
        })
      },
      sub(formName){
        var that = this;
        this.$refs[formName].validate((valid) => {
            if(valid) {
              if (that.imageUrl2==null){
                that.$message.error('请上传微信二维码');
                return false;
              } else {
                var params = {
                  name:that.form.name,
                  address:that.form.address,
                  phone:that.form.phone,
                  addimg:that.addimg,
                  name1:that.form.name1,
                  address1:that.form.address1,
                  phone1:that.form.phone1,
                  addimg1:that.addimg1,
                  name2:that.form.name2,
                  address2:that.form.address2,
                  phone2:that.form.phone2,
                  addimg2:that.addimg2,
                  name3:that.form.name3,
                  address3:that.form.address3,
                  phone3:that.form.phone3,
                  addimg3:that.addimg3,
                  email:that.form.email,
                  introduction:that.form.introduction,
                  wxgzh:that.form.wxgzh,
                  mpic:that.imageUrl2,
                  // longitude:经度
                  // latitude:纬度
                  // telpic:界面顶部电话图片
                  logo:that.imageUrl,
                  // mintelpic:界面顶部电话图片缩略图(方便查看,可不写)
                  // minlogo:logo(方便查看,可不写)
                };
                that.apiPost('/admin/setqyinfo', params).then((res) => {
                  this.handelResponse(res, (data) => {
                    that.$message.success('成功');
                    // that.$router.go(-1);
                    that.getDetails()
                  })
                })
              }
            }
        })
      },
      //上传图片
      handlePictureCardPreview(file) {
        this.form.imgUrl = file.url;
        this.imageUrl = file.url;
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data;
        this.formAdd.imgUrl = res.data;
        // this.imageUrl = this.form.bPic;
      },
      //上传图片
      handlePictureCardPreview2(file) {
        this.form.imgUrl = file.url;
        this.imageUrl2 = file.url;
      },
      handleAvatarSuccess2(res, file) {
        this.imageUrl2 = res.data;
        this.formAdd.imgUrl = res.data;
      },
      handlePictureCardPreviewA(file) {
        this.addimg = file.url;
      },
      handleAvatarSuccessA(res, file) {
        this.addimg = res.data;
      },
      handlePictureCardPreviewA1(file) {
        this.addimg1 = file.url;
      },
      handleAvatarSuccessA1(res, file) {
        this.addimg1 = res.data;
      },
      handlePictureCardPreviewA2(file) {
        this.addimg2 = file.url;
      },
      handleAvatarSuccessA2(res, file) {
        this.addimg2 = res.data;
      },
      handlePictureCardPreviewA3(file) {
        this.addimg3 = file.url;
      },
      handleAvatarSuccessA3(res, file) {
        this.addimg3 = res.data;
      },
    },
    mixins:[http]
  }
</script>

<style scoped>
  .el-upload--text {
    width: 180px;
  }
  .form-box{
    width: 100%;
  }
  img{
    width: 100%;
    height: 100%;
  }
</style>



